      // this.addProgramList = Array.from(addSet);
      addMap.forEach((value, key, self) => {
        let obj = {};
        obj.programId = key;
        obj.programName = value;
        this.addProgramList.push(obj);
      })

      console.log("从左向右移数组", this.addProgramList);
      this.deleteProgramList = [];

      this.countRight = this.channelList2.length;
      this.checkAllLeft = false;

      if (this.selectArr1.length == 0) {
