      if (userId) {
        let param = {
          userId:userId,
        }
  
        this.deletedFileName = [];
        this.deletedFileNameModal = false;
        api.saveUserProgram(param).then(
          res => {
            console.log("已被其他用户删除的视频文件名称", res.result);
            if (res.result && res.result.length != 0) {
              this.deletedFileName = res.result;
              this.deletedFileNameModal = true;
            } 

            // this.$Message.success('授权成功');
            this.$Message.success('操作成功');
            this.addProgramList = [];
            this.deleteProgramList = [];
          }
        )
      }
    },

    // /** 修改用户视频授权列表 */
    // updateVideoPermission(userId) {
    //   if (this.addProgramList && this.addProgramList.length > 0) {
    //     for (const key in this.addProgramList) {
    //       // const element = this.addProgramList[key].fileId;
    //       for (const key2 in this.deleteProgramList) {
    //         if (this.deleteProgramList[key2].id == this.addProgramList[key].id) {
    //           this.addProgramList.splice(key, 1);
    //           this.deleteProgramList.splice(key2, 1);
    //         }
    //       }
    //     }
    //   } else if (this.deleteProgramList && this.deleteProgramList.length > 0) {
    //     for (const key in this.deleteProgramList) {
    //       for (const key2 in this.addProgramList) {
    //         if (this.deleteProgramList[key].id == this.addProgramList[key2].id) {
    //           this.addProgramList.splice(key2, 1);
    //           this.deleteProgramList.splice(key, 1);
    //         }
    //       }
    //     }
    //   }
    //   let param1 = {
    //     user_id:userId,
    //     addProgramList:this.addProgramList,
    //     deleteProgramList:this.deleteProgramList,
    //   }

    //   api.updateUserVideoAuth(param1).then( 
    //     res1 => {
    //       this.$Message.success('授权成功');
    //       this.addProgramList = [];
    //       this.deleteProgramList = [];
    //     }
    //   )
    // },

    /** 左穿梭框页面改变 */
    changePageLeft(page) {
      this.pageNoLeft = page;
      this.videoSearchLeft1();
    },

    /** 右穿梭框页面改变 */
    changePageRight(page) {
      this.pageNoRight = page;
      this.videoSearchRight1();
    },













    startFunc (e) {
      this.$emit('on-start', e.oldIndex);
    },
    endFunc (e) {
      let movedRow = this.channelList2[e.oldIndex];
      this.channelList2.splice(e.oldIndex, 1);
      this.channelList2.splice(e.newIndex, 0, movedRow);
      this.$emit('on-end', {
        value: this.channelList2,
        from: e.oldIndex,
        to: e.newIndex
      });
    },
    chooseFunc (e) {
      this.$emit('on-choose', e.oldIndex);
    },
    getAllRegionList(){
      let param = {
        pageNo:-1,
        regionName: ""
      }
      api.regionJson(param).then(res=>{
        this.regionList = res.result
      })
    },
    changeLeft(){
      this.channelList = []
      this.channelSearchLeft()
      this.copyStatus = true
      this.countLeft = 0
    },
    changeRight(){
      this.channelList2 = []
      this.channelSearchRight()
      this.copyStatus = true
      this.countRight = 0
      //获取地区信息
      console.log(this.channelName2)
      for(let item of this.regionList){
        if(item.name === this.channelName2){
          this.region = item
        }
      }
    },
    //点击保存按钮触发保存排序
    saveSort(){
      this.loadingRight = true
      let channelList = []
      for(let index in this.channelList2){
         console.log(index)
         let obj ={
           id:this.channelList2[index].id,
           channelSort:index/1+1+''
         }
         channelList.push(obj)
      }
      let param = {
        id: this.region.id,
        locationId: this.region.locationId,
        channelList
      }
      api.sortCommit(param).then(res=>{
        this.loadingRight = false
        this.$Message.success(this.$t('sortSaveSuccess'))
      }).catch(err=>{
         this.loadingRight = false
      })

    },

    // 查询穿梭框-左边
    channelSearchLeft () {
      if(!this.channelName){
        return false
      }
      this.clickStatus = false
      let param = {
         locationNameStrict : this.channelName,
      }
      this.searchBtnLeft = true
      this.loadingLeft = true
      api.regionChannelJson(param).then(res => {
        this.countLeft = res.result.length
        this.channelList = res.result
        this.searchBtnLeft = false
        this.loadingLeft = false
      },err => {
        this.searchBtnLeft = false
        this.loadingLeft = false
      })
    },

    // 查询穿梭框-右边
    channelSearchRight () {
      if(!this.channelName2){
        return false
      }
      // this.regionJsonRight()   //精准查询地区
      this.clickStatus = false
      let param = {
         locationNameStrict : this.channelName2,
      }
      // console.log(param)
      this.searchBtnRight = true
      this.loadingRight = true
      api.regionChannelJson(param).then(res => {
        // console.log(res)
        this.countRight = res.result.length
        this.channelList2 = res.result
        this.searchBtnRight = false
        this.loadingRight = false
      },err => {
        this.searchBtnRight = false
        this.loadingRight = false
      })
    },

    stopCopy(){
      this.flag = true
      if ( !this.channelName && !this.channelName2 )
      {
        this.$Message.error('请选择来源地区和目标地区')
        this.flag = false
        return false
      }
      else if ( !this.channelName )
      {
        this.$Message.error('请选择来源地区，再进行操作')
        this.flag = false
        return false
      }
      else if ( !this.channelName2 )
      {
        // this.$Message.error(this.$t('live_manage.enterTheToAndThen'))
        this.flag = false
        return false
      }
      else if ( !this.channelList.length&&!this.channelList2.length )
      {
        // this.$Message.error(this.$t('theComeAndToRegionHasNoneList'))
        this.flag = false
        return false
      }
      else if ( !this.channelList.length )
      {
        // this.$Message.error(this.$t('theComeRegionHasNoneList'))
        this.flag = false
        return false
      }
      else if ( !this.channelList2.length )
      {
        // this.$Message.error(this.$t('theToRegionHasNoneList'))
        this.flag = false
        return false
      }
      else if (this.channelName === this.channelName2)
      {
        // this.$Message.error(this.$t('selectDifferentRegion'))
        this.flag = false
        return false
      }
    },

    //清理左边输入框
    handleCancelLeft () {
      this.channelName = ''
      this.channelList = []
      this.countLeft = 0
      this.copyStatus = false
    },

    //清理右边输入框
    handleCancelRight () {
      this.channelName2 = ''
      this.channelList2 = []
      this.countRight = 0
      this.copyStatus = false
      this.clickStatus = false
    },

    // 穿梭框往右
    channel_right () {
       this.stopCopy()
       if(!this.flag){
         return false
       }
       console.log('哈哈',this.channelList2)
       //记录左侧右侧频道列表数据
       let left = JSON.parse(JSON.stringify(this.channelList))
       let right = JSON.parse(JSON.stringify(this.channelList2))
       console.log('left',left,'right',right)
       let arr = JSON.parse(JSON.stringify(right))
      console.log('arr',arr)
       let arrRight = []
       for(let key in right){
         for(let key1 in left){
           if(right[key].name === left[key1].name){
              //记录左侧的所在索引
              right[key].index = key1
              arrRight.push(right[key])
              for(let key2 in arr){
                if(arr[key2].name === right[key].name){
                  arr.splice(key2,1)
                }
              }
           }
         }
       }
       arrRight.sort((val,val1)=>{
         return val.index - val1.index
       })
       console.log('arr',arr)
       for(let item of arr){
         arrRight.push(item)
       }
      // for(let item of right){
      //   if(!item.index){
      //     arrRight.push(item)
      //   }
      // }
       console.log(arrRight)
       this.channelList2 = arrRight
    },

    //  点击全选
    checkList () {
      this.handleSelectAll(this.checktypeLeft, 'selectionLeft')
    },

    //  点击全选
    checkList2 () {
      this.handleSelectAll(this.checktypeRight, 'selectionRight')
    },

    handleSelectAll (status, obj) {
      this.$refs[obj].selectAll(status)
    },
  }
}
